import React, { useState } from 'react';
import axios from 'axios';

export default function CategoryAdd() {
    let [category_title, setCategory_title] = useState("")
    let [category_description, setCategory_description] = useState("")

    const AddNewCategory = async () => {
        let formField = new FormData()

        formField.append('category_title', category_title)
        formField.append('category_description', category_description)

    function readCookie(name) {
    let nameEQ = name + "=";
    let ca = document.cookie.split(';');
    for(let i=0;i < ca.length;i++) {
        let c = ca[i];
        while (c.charAt(0)===' ') c = c.substring(1,c.length);
        if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length,c.length);
    }
    return null;
}

    let csrftoken = readCookie('csrftoken');

    if (category_title) {
        await axios({
            headers: {"X-CSRFToken":csrftoken },
            method: 'post',
            url: 'http://127.0.0.1:8000/api/v1/category/',
            data: formField
        }).then(response => {
            alert(`Category ${category_title} has been added`);
            })
        }
    }
    return (
        <div className="container sm">
            <form className="form-control p-2" onSubmit={AddNewCategory}>
                <input
                    type="text"
                    className="form-control my-2"
                    placeholder="Category"
                    name="category_title"
                    value={category_title}
                    onChange={(e) => setCategory_title(e.target.value)}
                    />
                <input
                    type="text"
                    className="form-control"
                    placeholder="Description"
                    name="category_description"
                    value={category_description}
                    onChange={(e) => setCategory_description(e.target.value)}
                    />
                <button type="submit" className="btn btn-secondary my-2">Submit</button>
            </form>
        </div>
    )
}