import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import UserNavbar from './components/navbar/userNavbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import CategoryDetail from './components/detail/categoryDetail';
import OwnerDetail from './components/detail/ownerDetail';
import DepartamentDetail from './components/detail/departamentDetail';
import CategoryAdd from './components/forms/categoryAdd';
import OwnerAdd from './components/forms/ownerAdd';
import InventoryTable from './components/table/inventoryTable';
import InventoryDetail from './components/detail/inventoryDetail';


class App extends React.Component {
  render() {
    return (
    <Router>
      <UserNavbar />
      <br/>
      <br/>
        <div className="container">
          <Switch>
            <Route path="/home"><InventoryTable /></Route>
            <Route path="/inventory/:id" exact component={InventoryDetail} />
            <Route path="/owner/:id" exact component={OwnerDetail} />
            <Route path="/category/:id" exact component={CategoryDetail} />
            <Route path="/departament/:id" exact component={DepartamentDetail} />
            <Route path="/addcategory"><CategoryAdd /></Route>
            <Route path="/addowner"><OwnerAdd /></Route>
          </Switch>
      </div>
    </Router>
    )
  }
}

export default App;
