import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Card } from 'react-bootstrap';


function OwnerDetail({match}) {
    const [departament, setDepartament] = useState({})
    const id = match.params.id

    useEffect(() => {
        axios({
            method: 'GET',
            url: `http://127.0.0.1:8000/api/v1/departament/${id}`
        }).then(response => {
            setDepartament(response.data)
        })
    }, [id])
    return(
        <div className="container">
          <Card border="dark">
            <Card.Header>{departament.id}</Card.Header>
            <Card.Body>
              <Card.Title>{departament.departament_title}</Card.Title>
              <Card.Text>
                {departament.departament_description}
              </Card.Text>
            </Card.Body>
          </Card>
        </div>
    )
}

export default OwnerDetail;